/* Bootstrap Alerts */

.alert {
  font-size: $default-font-size;
  i {
    font-size: 1.25rem;
    margin-right: 1.25rem;
    vertical-align: middle;
    line-height: .5;
  }
}

@each $color, $value in $main-colors {
  .alert-#{$color} {
    @include alert-variant(rgba(t-color($color), .2), t-color-level($color, 1), t-color-level($color, 3));
  }
}

@each $color, $value in $main-colors {
  .alert-fill-#{$color} {
    @include alert-variant(t-color($color), t-color($color), color(white));
  }
}
